import { Lightning, MediaPlayer } from 'wpe-lightning-sdk'


export default class Player extends Lightning.Component  {
    
    static _template() {
        return {
            MediaPlayer: {
                type: MediaPlayer,
            },
        }
    }
    
    _firstActive() {
        this.tag('MediaPlayer').updateSettings({consumer: this});
        this.tag('MediaPlayer').open('http://commondatastorage.googleapis.com/gtv-videos-bucket/sample/BigBuckBunny.mp4');
    }
    
    // Canplay
    $mediaPlayerCanplay () {
        console.log("Canplay")
    }
    
    // Playing
    $mediaplayerPlaying () {
        console.log("Playing")
    }
    
    // Ended
    $mediaplayerEnded() {
        console.log("Ended")
        this.tag('MediaPlayer').seek(0)
    }
    
    // Timeupdate
    $mediaplayerTimeupdate() {
        console.log("Timeupdate")
    }
    
    // Error
    $mediaplayerError() {
        console.log("Error")
    }
    
    // Loadeddata
    $mediaplayerLoadeddata() {
        console.log("Loadeddata")
    }
    
    // Play
    $mediaplayerPlay() {
        console.log("Play")
    }
    
    // Pause
    $mediaplayerPause() {
        console.log("Pause")
    }
    
    // Loadstart
    $mediaplayerLoadstart() {
        console.log("Loadstart")
    }
    
    // Seeking
    $mediaplayerSeeking() {
        console.log("Seeking")
    }

    // Seeked
    $mediaplayerSeeked() {
        console.log("Seeked")
    }

    // Encrypted
    $mediaplayerEncrypted() {
        console.log("Encrypted")
    }
}